import { ChangeDetection } from '@angular/cli/lib/config/schema';
import { ChangeDetectionStrategy, Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Product } from '../interface/product';

@Component({
  selector: 'app-stateless',
  templateUrl: './stateless.component.html',
  styleUrls: ['./stateless.component.css'],
  changeDetection: ChangeDetectionStrategy.OnPush // As√≠ s√≥lo se intanciar√° una sola vez
})
export class StatelessComponent implements OnInit {

  @Output () cursomatriculado: EventEmitter<Product> = new EventEmitter();// Nos dir√° en que curso se ha matriculado

  @Input () product: Product; 
  public matricula: string;
  private disabled: boolean;

  constructor() { }

  ngOnInit(): void {
    // Damos texto al bot√≥n
    this.matricula = 'Matricularse';
  }

  // Cuando se matriculan, se desativa el bot√≥n y se muestra que est√°n matriculados
  matricularse() {
    this.disabled = true;
    this.matricula = 'Usted est√° matriculado';
    this.cursomatriculado.emit(this.product); // Guardamos/enviamos el curso en el que se ha matriculado al componente padre
  }

  // Devuelve si est√° deshabilitado
  isDisabled() {
    return this.disabled;
  }

}
